/* small helpers */
.container {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.marquee {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.marquee__inner {
  display: flex;
  gap: 3rem;
  width: -moz-max-content;
  width: max-content;
  padding-left: 100%;
  animation: marquee 15s linear infinite;
  font-size: 4.5rem; /* Adjust size */
  font-weight: 500;
  white-space: nowrap;
}

.marquee__inner span {
  margin-right: 3rem;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50%);
  }
}

/* Auth Modal Styles */
.auth-modal-overlay {
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
}

.auth-modal-content {
  max-height: 90vh;
  overflow-y: auto;
}

/* Custom scrollbar for auth modal */
.auth-modal-content::-webkit-scrollbar {
  width: 6px;
}

.auth-modal-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.auth-modal-content::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.auth-modal-content::-webkit-scrollbar-thumb:hover {
  background: #555;
}

